.input-title {
    width: 100%;
    height: 1.7rem;
    font-size: 1.2rem;
    font-weight: bold;
    border: none;

    &:focus {
        background: #ddd;
    }
}

.editable-title-container {
    position: relative;
    display: flex;

    .editable-title {
        height: 1.7rem;
        flex-grow: 1;
        font-size: 1.2rem;
        font-weight: bold;
        height: 1.5em;
        border: none;
        cursor: pointer;
        transition: background-color 0.2s;
    }

    .list-button {
        height: 1.5em;
        border: none;
        cursor: pointer;
        transition: background-color 0.2s;

        &:hover {
            background-color: #ddd;
        }
    }

    .menu-card {
        position: absolute;
        right: 0;
        background-color: #fff;
        border: 1px solid #ddd;
        border-radius: 4px;
        z-index: 999;
        list-style-type: none;
        cursor: default;

        li {
            color: #333;
            font-size: 0.875rem;
            border-bottom: 1px solid #ddd;
            padding: 0.5rem 0.5rem 0.3rem;

            &:hover {
                background-color: #ddd;
            }
        }
    }
}